'use strict'

const test = require('ava')
const proxyquire = require('proxyquire')
const sinon = require('sinon')
const agentFixtures = require('./fixtures/fx_agent')
const metricFixtures = require('./fixtures/fx_metric')

let config = {
  logging: function () {}
}

let AgentStub = null
let MetricStub = null

let db = null
let uuid = 'yyy-yyy-yyy'
let type = 'CPU'

let sandbox = null

let uuidArgs = {
  where: {
    uuid
  }
}

let newMetric = {
  agentId: 1,
  type: 'CPU',
  value: '23%'
}

test.beforeEach(async () => {
  sandbox = sinon.sandbox.create()

  AgentStub = {
    hasMany: sandbox.spy()
  }

  MetricStub = {
    hasMany: sandbox.spy()
  }

  // Modelo findOne Stub
  AgentStub.findOne = sandbox.stub()
  AgentStub.findOne.withArgs(uuidArgs).returns(Promise.resolve(metricFixtures.findByUuid))

  MetricStub.create = sandbox.stub()
  MetricStub.create.withArgs(newMetric).returns(Promise.resolve({toJSON () {return newMetricArgs }}))

  const setupDatabase = proxyquire('../index.js', {
    './models/agent': () => AgentStub,
    './models/metric': () => MetricStub
  })
  db = await setupDatabase(config)
})

test.afterEach(() => {
  sandbox && sinon.sandbox.restore()
})

test('Metric', t => {
  t.truthy(db.Metric, 'Servicio Metric Existe')
})

test.serial('Metric#create', async t => {
  let metric = await db.Metric.create(newMetric)

  t.true(AgentStub.findOne.called, 'Funcion findOne esta siendo llamada')
  t.true(AgentStub.findOne.calledOnce, 'Funcion findOne esta siento llamada una vez')
  t.true(MetricStub.create.called, 'Funcion create esta siendo llamada')
  t.true(MetricStub.create.calledOnce, 'Funcion create esta siento llamada una vez')

  t.deepEqual(metric, newMetric, 'La Metrica creada es perfectamente igual al que le envi√©')
})

